import React, { useState } from 'react';
import ChapterReader from '@/components/novel/chapter-reader';
import { Project, Chapter } from '@/types/editor';

// Mock data for demonstration
const novels: Project[] = [
  {
    id: 1,
    slug: 'the-lost-realm',
    title: 'The Lost Realm',
    lastChapter: 'Prologue',
    lastEdited: '2025-09-10',
    wordCount: 45000,
    status: 'Draft',
    genre: 'Fantasy',
    chapters: 12,
    words: 45000,
    views: 1200,
    collections: 100,
    rating: 4.5,
    lastUpdated: '2025-09-11',
    cover: '',
    description: 'An epic fantasy adventure.',
    tags: ['adventure', 'magic'],
    progress: 80,
  },
  {
    id: 2,
    slug: 'city-of-glass',
    title: 'City of Glass',
    lastChapter: 'Chapter 20',
    lastEdited: '2025-09-09',
    wordCount: 80000,
    status: 'Completed',
    genre: 'Dystopian',
    chapters: 20,
    words: 80000,
    views: 3000,
    collections: 250,
    rating: 4.8,
    lastUpdated: '2025-09-10',
    cover: '',
    description: 'A dystopian thriller.',
    tags: ['thriller', 'future'],
    progress: 100,
  },
];

const chapters: Chapter[] = [
  { id: 101, title: 'Prologue', content: 'In the beginning...', order: 1, wordCount: 500, publishedAt: '2025-09-11', status: 'completed' },
  { id: 102, title: 'Chapter 1', content: 'The journey starts...', order: 2, wordCount: 1200, publishedAt: '2025-09-12', status: 'draft' },
];

export default function NovelPage() {
  const [selectedNovel, setSelectedNovel] = useState<Project | null>(null);
  const [selectedChapter, setSelectedChapter] = useState<Chapter | null>(null);
  const [mode, setMode] = useState<'editor' | 'reader'>('reader');

  if (selectedChapter && selectedNovel) {
    return (
      <div className="p-6">
        <button onClick={() => setSelectedChapter(null)} className="mb-4">Back to Chapters</button>
        <ChapterReader
          chapter={selectedChapter}
          mode={mode}
          onModeChange={setMode}
        />
      </div>
    );
  }

  if (selectedNovel) {
    return (
      <div className="p-6">
        <button onClick={() => setSelectedNovel(null)} className="mb-4">Back to Novels</button>
        <h2 className="text-2xl font-bold mb-4">{selectedNovel.title} - Chapters</h2>
        <ul>
          {chapters.map(ch => (
            <li key={ch.id}>
              <button onClick={() => setSelectedChapter(ch)} className="underline text-blue-600">
                {ch.title}
              </button>
            </li>
          ))}
        </ul>
      </div>
    );
  }

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6">Novels</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {novels.map(novel => (
          <div
            key={novel.id}
            className="border rounded p-4 shadow cursor-pointer hover:bg-gray-50"
            onClick={() => setSelectedNovel(novel)}
          >
            <h2 className="text-xl font-bold mb-2">{novel.title}</h2>
            <p className="text-gray-600 mb-1">{novel.description}</p>
            <span className="text-xs text-gray-500">Status: {novel.status}</span>
          </div>
        ))}
      </div>
    </div>
  );
}
